<template>
  <div class="calculate-setting">
    <page-layout :title="title">
        <a-tabs slot="headerContent" :activeKey="active"  @change="callback">
        <a-tab-pane
          tab="保司设置"
          key="1"
        >
        </a-tab-pane>
        <a-tab-pane
          tab="短信模板"
          key="2"
        >
        </a-tab-pane>
        <a-tab-pane
          tab="允许险种"
          key="3"
        >
        </a-tab-pane>
          <!-- :disabled="!isDisabled" -->
      </a-tabs>
      <router-view />
      <!-- <insurance-api /> -->
    </page-layout>
  </div>
</template>

<script>
import ATabs from 'ant-design-vue/lib/tabs'
const ATabPane = ATabs.TabPane
import PageLayout from '@/components/Page/PageLayout'
import { getCalculateSetting } from '@/api/calculateSetting'
export default {
  components: {
    PageLayout,
    ATabs,
    ATabPane
  },
  data(){
      return{
          activeKey:'1'
      }
  },
  mounted(){
    getCalculateSetting().then((result) => {
    }).catch((err) => {
    });
  },
  computed: {
    title() {
      return this.$route.meta.title;
    },
    active(){
        switch (this.$route.path) {
            case '/settings/calculatesetting/insurancesettings':
            return '1'
            case '/settings/calculatesetting/msgtemplate':
            return '2'
            case '/settings/calculatesetting/allowinstype':
            return '3'
            default:
            return '1'
        }
    },
  },
  methods:{
    callback(key) {
      switch (key) {
          case '1':
          this.$router.push('/settings/calculatesetting/insurancesettings')
          break
          case '2':
          this.$router.push('/settings/calculatesetting/msgtemplate')
          break
          case '3':
          this.$router.push('/settings/calculatesetting/allowinstype')
          break
          default:
          this.$router.push('/dashboard/workplace')
      }
    },

  }
};
</script>

<style>
.calculate-setting .ant-tabs-bar{
    margin-bottom: 0
}
</style>
</<style scoped>
div.content div.page-header-index-wide > div {
    padding: 0;
    margin-bottom: 24px;
    background-color: transparent;
}
</style>




